.product{
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    background-color: bisque;
    width: fit-content;
    justify-content: center;
    border-radius: 4%;
    padding: 1%;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    max-height: fit-content;
}
.product img{
    width: 15rem;
    min-height: 17rem;
}
#pname{
    font-size: 30px;
    max-width: 15rem;
    margin: auto;
}
#pprice{
    font-size: 25px;
    /* margin: auto; */
}
#aTC{
    font-size: 30px;
    padding: 5%;
    border-radius: 5rem;
    border: 1px solid darkgoldenrod;
}